import React, {useEffect, useState} from "react";
import {useTranslation} from 'react-i18next';
import "./index.css";
import API from "../../API";
import Loader from "react-loader-spinner";

export default function CoachQuizContainer(props) {
    const {t,} = useTranslation();
    const [prePostTestInfo, setPrePostTestInfo] = useState()
    const [testRegisterInfo, setTestRegisterInfo] = useState({
        name: ""
    })

    useEffect(() => {
        const fetchData = async () => {
            const result = await API.getPostTestsInfo(props.match.params.testId);
            console.log(result.data)
            setPrePostTestInfo(result.data)
        };
        fetchData();
        // eslint-disable-next-line
    }, []);

    const handleChange = (e) => {
        e.persist()
        setTestRegisterInfo({...testRegisterInfo, [e.target.name]: e.target.value})
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        API.postPreTestRegistration(props.match.params.testId, testRegisterInfo)
            .then((response) => {
                console.log(response.data)
            })
            .catch(error => {
                alert("Заполните все поля")
            })
    }

    return (
        <section className="coach-test-container base-container bottom-padding flex-col justify-start align-start">
            {prePostTestInfo ? <>
                <h1>{prePostTestInfo.test.title}</h1>
                <p>{prePostTestInfo.test.description || "Без описания"}</p>

                {/*<div className="flex-col align-start justify-start">*/}
                {/*    <h2>Ваша ссылка</h2>*/}
                {/*    <a href={`http://localhost:3000/pre-post/${props.match.params.testId}`}>*/}
                {/*        http://localhost:3000/pre-post/{props.match.params.testId}*/}
                {/*    </a>*/}
                {/*</div> :*/}
                <form className="flex-col" onSubmit={handleSubmit}>
                    <div className="form-container grid-2">
                        <div className="flex-col ">
                            <label>{t("coachSectionTestName")}</label>
                            <input name="name" type="text" onChange={handleChange}/>
                        </div>
                        <div className="flex-col ">
                            <label>{t("coachSectionTestSoName")}</label>
                            <input name="soName" type="text"/>
                        </div>
                        <div className="flex-col ">
                            <label>{t("coachSectionTestDate")}</label>
                            <input name="date" type="date"/>
                        </div>
                        <div className="flex-col ">
                            <label>{t("coachSectionTestPlace")}</label>
                            <input name="place" type="text"/>
                        </div>
                        <div className="flex-col ">
                            <label>{t("coachSectionTestCount")}</label>
                            <input name="count" type="number"/>
                        </div>
                    </div>
                    <button>{t("coachSectionTestButton")}</button>
                </form>
            </> : <Loader
                type="TailSpin"
                color="#00BFFF"
                height={50}
                width={50}
                style={{margin: "auto"}}
                timeout={5000}
            />
            }
        </section>
    );
}
