import React, {useEffect, useState} from "react";
import SanitizedHTML from "react-sanitized-html";
import AwesomeSlider from "react-awesome-slider";
import "react-awesome-slider/dist/styles.css";
import BlogBanner from "../../components/blog-banner";

import "./index.css";
import API from "../../API";

export default function CoachMaterialArticle(props) {
    const [articleInfo, setArticleInfo] = useState([]);
    useEffect(() => {
        window.scrollTo(0, 0);

        const fetchData = async () => {
            const result = await API.getArticleByKey(
                props.match.params.materialId
            );
            setArticleInfo(result.data.blog);
        };
        fetchData();
        // eslint-disable-next-line
    }, []);

    useEffect(() => {
        window.scrollTo(0, 0);
    }, []);
    return (
        <>
            <BlogBanner
                blogBannerInfo={{
                    header: articleInfo.title,
                    backImg: "none",
                    backColor: "#9B0A00",
                }}
            />
            <div className="base-container base-padding blog-description">
                <div>

                    <SanitizedHTML
                        allowedTags={[
                            "h2",
                            "h4",
                            "h5",
                            "h6",
                            "blockquote",
                            "p",
                            "a",
                            "ul",
                            "ol",
                            "nl",
                            "li",
                            "b",
                            "i",
                            "u",
                            "strong",
                            "em",
                            "strike",
                            "code",
                            "hr",
                            "br",
                            "div",
                            "table",
                            "thead",
                            "caption",
                            "tbody",
                            "tr",
                            "th",
                            "td",
                            "pre",
                            "img",
                            "video",
                            "source",
                            "span",
                            "iframe",
                            "sub",
                            "sup",
                        ]}
                        allowedAttributes={{
                            h2: ["style"],
                            h4: ["style"],
                            h5: ["style"],
                            h6: ["style"],
                            blockquote: ["style"],
                            p: ["style"],
                            a: ["style"],
                            ul: ["style"],
                            ol: ["style"],
                            nl: ["style"],
                            li: ["style"],
                            b: ["style"],
                            i: ["style"],
                            u: ["style"],
                            strong: ["style"],
                            em: ["style"],
                            strike: ["style"],
                            code: ["style"],
                            hr: ["style"],
                            br: ["style"],
                            div: ["style"],
                            table: ["style"],
                            thead: ["style"],
                            caption: ["style"],
                            tbody: ["style"],
                            tr: ["style"],
                            th: ["style"],
                            td: ["style"],
                            pre: ["style"],
                            img: ["style", "src"],
                            video: ["style"],
                            source: ["style"],
                            span: ["style"],
                            iframe: ["style"],
                            sub: ["style"],
                            sup: ["style"],
                        }}
                        html={articleInfo.content}
                    />
                </div>
                {articleInfo.slider && (
                    <AwesomeSlider infinite={true} bullets={false}>
                        {articleInfo.slider.map((item, id) => (
                            <div key={id}>
                                <img src={item} alt={id}/>
                            </div>
                        ))}
                    </AwesomeSlider>
                )}
            </div>
        </>
    );
}
