import React, {useEffect, useState} from "react";
import "./index.css";
import API from "../../API";

export default function CoachManual(props) {
    const [coachManual, setCoachManual] = useState();

    useEffect(() => {
        const fetchData = async () => {
            const result = await API.getCoachManual(props.language);
            setCoachManual(result.data.leaderships);
        };
        fetchData();
        // eslint-disable-next-line
    }, [props.language]);
    return (
        <section className="manual-section bottom-padding base-container">
            {coachManual ? coachManual.map((item, id) => (
                <div key={id}>
                    <h2>{item.title}</h2>
                    <h3>{item.text}</h3>
                </div>
            )) : <></>}
        </section>
    );
}
