import React, {useEffect, useState} from "react";
import {useTranslation} from 'react-i18next';
import "./index.css";
import API from "../../API";

export default function CoachPersonalForm() {
    const {t} = useTranslation();
    const [coachData, setCoachData] = useState({})
    const [formData, setFormData] = useState()

    useEffect(() => {
        const fetchData = async () => {
            const result = await API.getCoachInfo(window.localStorage.getItem("user"));
            setCoachData(result.data.user);
            setCoachData({
                workplace: result.data.user.workplace,
                phone: result.data.user.phone,
                email: result.data.user.email,
                language: result.data.user.language,
                password: result.data.user.password
            })
        };
        fetchData();
        // eslint-disable-next-line
    }, []);


    const handleInput = (e) => {
        setFormData({...formData, [e.target.name]: e.target.value})
    }

    // const handleSubmit = (e) => {
    //     e.preventDefault()
    //     API.postSessionForm(formData)
    //         .then(response => console.log(response))
    //         .catch(error => console.log(error))
    // }

    return (
        <div className="coach-personal-form flex-col align-start">
            <h2>{t("coachSectionPersonalData")}</h2>
            <form className="grid-2">
                <div className="flex-col">
                    <label>{t("coachSectionWork")}</label>
                    <input name="workplace" type="text" onChange={handleInput} defaultValue={coachData.workplace}/>
                </div>
                <div className="flex-col">
                    <label>{t("coachSectionPhone")}</label>
                    <input name="phone" type="text" onChange={handleInput} defaultValue={coachData.phone}/>
                </div>
                <div className="flex-col">
                    <label>Email</label>
                    <input name="email" type="text" onChange={handleInput} defaultValue={coachData.email}/>
                </div>
                <div className="flex-col">
                    <label>{t("coachSectionLanguages")}</label>
                    <input name="language" type="text" onChange={handleInput} defaultValue={coachData.language}/>
                </div>
                <div className="flex-col">
                    <label>{t("coachAuthLabel2")}</label>
                    <input name="password" type="text" onChange={handleInput} defaultValue={coachData.password}/>
                </div>
                <div/>
                <button type="submit">{t("coachSectionPersonalButton")}</button>
            </form>
        </div>
    );
}
